<template>
    <div  :style="bgpic" style="min-width: 1000px;min-height: 850px;height: 100vh">
    <el-container v-loading="loading"element-loading-text="loading......" element-loading-spinner="el-icon-loading" style="height: 100%">
        <el-header style="height: 70px;;width: 100%;min-width: 1000px;padding-top: 10px;">
            <div style="height: 70px;width: 60%;margin-left: 20%;box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);text-align: center;background-color: white">
                <span @click="getfilelist()" style="font-family: Helvetica Neue;font-size: 25px;line-height: 70px;float: left;padding-left: 5%"><i class="el-icon-folder-opened"></i> CloudFolder</span>
                <div style="float: left;margin-left: 15%;line-height: 70px">
                    <span style="font-size: 15px">输入文件名：</span>
                    <el-input style="width: 100px" v-model="filename"></el-input><el-button style="" icon="el-icon-search" @click="searchfile()"></el-button>
                </div>
                <span style="line-height:70px;float: right;padding-right: 5%">
                <el-dropdown>
                  <span class="el-dropdown-link" style="color: black;display: block">
                      {{username}}
                      <i class="el-icon-arrow-down el-icon--right"></i>
                  </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item >
                            <el-button type="text" @click="logout()" >退出登录</el-button>
                        </el-dropdown-item>

                    </el-dropdown-menu>
                </el-dropdown>
                </span>
            </div>
        </el-header>
        <el-main style=";margin-top: 10px;;;width: 100%;min-width: 1000px">
            <div style=";width: 58%;margin-left: 21%">
                <div style="height: 500px;box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);">
                    <el-table :data="tableData" style="width: 100%" height="500px" >
                        <el-table-column  label="*" width="100%"><template><i class="el-icon-folder-opened"></i></template></el-table-column>
                        <el-table-column prop="file_name" label="文件名" sortable></el-table-column>
                        <el-table-column prop="file_size" label="文件大小" sortable ></el-table-column>
                        <el-table-column prop="file_time" label="上传时间" sortable></el-table-column>
                        <el-table-column  label="文件操作"width="150">
                            <template slot-scope="scope">
                                <el-button type="text" @click="downloadfile(scope.row)">下载</el-button>
                                <el-button type="text" style="color: #67C23A" @click="sharefile(scope.row)">分享</el-button>
                                <el-button type="text" style="color: red" @click="remove(scope.row)">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
                <div style="width:100%;margin-top:10px;height: 200px; ">
                    <div style="width: 60%;float: left;box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);height: 200px;background-color: white;text-align: center">
                        <span style="font-size:20px;display: block;padding-top: 10px">文件上传</span>

                        <el-upload class="upload-demo" ref="upload" style="padding-top: 15px" :auto-upload="false" :action="doUpload" :limit=1 :before-upload="beforeUpload" :on-change = "ctrlfile">
                            <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
                            <el-button style="margin-left: 10px;" size="small" type="success" @click="bUpload">上传到服务器</el-button>
                            <div slot="tip" class="el-upload__tip">注意:文件大小不要超过5MB</div>
                        </el-upload>
                    </div>
                    <div style="width: 39%;float: right;box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);height: 200px;background-color: white">

                        <span style="color: #606266;padding-left: 25%; height: 20%;margin-top: 10%;display: block"><i class="el-icon-folder"></i> 总空间：<span style="color: #409EFF">{{this.userCapacity.total}}</span></span>
                        <span style="color: #606266;padding-left: 25%;height: 20%;display: block"><i class="el-icon-folder-add"></i> 已用空间：<span style="color: #F56C6C">{{this.userCapacity.used}}</span></span>
                        <span style="color: #606266;padding-left: 25%;height: 20%;display: block"><i class="el-icon-folder-remove"></i> 剩余空间：<span style="color: #67C23A">{{this.userCapacity.remain}}</span></span>

                    </div>
                </div>
            </div>
        </el-main>
    </el-container>
    </div>
</template>

<script>
    import SparkMD5 from 'spark-md5'
    export default {

        data() {
            return {
                userCapacity:{total:"",used:"",remain:""},
                filename:'',
                tableData: [],
                loading: false,
                filehash : '',
                username:"aaa",
                bgpic:{
                    backgroundImage:'url(' + require('../assets/filelogin.jpg') + ')',
                    backgroundRepeat:'no-repeat',
                    backgroundSize:'100% 100%',


                },
            }
        },
        created(){
            this.getfilelist()
            this.getUserCapacity()
            this.ajaxControl()
            this.getusername()
        },

        methods:{
            //对文件进行计算
            ctrlfile(file, fileList){

                let that = this
                //只要有变化，清空data的hash
                this.filehash = ""
                //计算文件的hash
                let fileRaw = file.raw
                let blobSlice = File.prototype.slice || File.prototype.mozSlice || File.prototype.webkitSlice
                let fileReader = new FileReader()
                let chunkSize = 2097152
                let chunks = Math.ceil(file.size / chunkSize)
                let currentChunk = 0
                let spark = new SparkMD5()
                fileReader.onload = function (e) {
                    spark.appendBinary(e.target.result)
                    currentChunk++
                    if (currentChunk < chunks) {
                        loadNext()
                    } else {
                        let filehash  = spark.end()
                        //console.log('文件的hash：', fhash)
                        that.filehash = filehash
                       // console.log("读出来的文件hash"+that.filehash)

                    }
                }
                fileReader.onerror = function () {
                    console.warn('FileReader error.')
                }
                function loadNext () {
                    let start = currentChunk * chunkSize
                    let end = ((start + chunkSize) >= file.size) ? file.size : start + chunkSize
                    // 注意这里的 fileRaw
                    fileReader.readAsBinaryString(blobSlice.call(fileRaw, start, end))
                }
                loadNext()
            },

            getUserCapacity(){
                const url = "/pan/file/capacity"
                this.$ajax.get(url,
                    {
                        headers:
                            {
                                "web-token":window.sessionStorage.getItem("web-token"),
                            }
                    }
                ).then(res=>{
                        if (res.data.code == 200){
                            this.userCapacity.total = res.data.result.total_capacity+"KB"
                            this.userCapacity.remain = res.data.result.remain_capacity+"KB"
                            this.userCapacity.used = res.data.result.used_capacity+"KB"
                        }else {
                            this.$message.error("登陆失效")
                            window.sessionStorage.clear()
                            this.$router.push("/")
                        }
                    }).catch(err=>{
                    this.$message.error("请求网络出错")
                })
            },

            bUpload(){
                this.$refs.upload.submit();
            },
            beforeUpload(file){
                //判读文件大小
                if (file.size > 5210*1024){
                    this.$message.error("文件过大")
                    return;
                }
                //判断剩余容量够不够
                //判断文件的hash
                if (this.filehash == ""){
                    this.$message.info("稍后，正在准备文件数据")
                    return;
                }
                //获取文件名
                var fname = file.name
                if (fname == ""){
                    this.$message.error("没有文件")
                    return
                }

                let fd = new FormData();
                //需要将文件名转码到utf8
                fd.append((encodeURI(fname)) ,file);//传文件
                const url = "/pan/file/upload"
                this.$ajax.post(url,fd,
                    {
                        headers:{
                            "web-token":window.sessionStorage.getItem("web-token"),
                            "filename":(encodeURI(fname)),
                            "filehash":this.filehash
                        }
                    }).then(res=>{
                    if (res.data.code == 200){
                        this.getfilelist()
                        this.getUserCapacity()
                        this.$message.info("上传成功")
                    }else if (res.data.code == 4022){
                        this.$message.error("文件过大")
                    }else if (res.data.code == 4024){
                        this.$message.error("您的剩余空间不足")
                    }else {
                        this.$message.error("登陆失效")
                        window.sessionStorage.clear()
                        this.$router.push("/")
                    }
                }).catch(err=>{
                    this.$message.error("上传网络出错")
                })
            },
            getfilelist(){
                const url= "/pan/file/getlist"
                this.$ajax.get(url,
                    {
                        headers:{"web-token":window.sessionStorage.getItem("web-token")}
                    }).then(res=>{
                        if (res.data.code == 200){
                            if (res.data.result == null){
                                this.$message.info("文件夹为空哦")
                                this.tableData = res.data.result
                            }else {
                                this.tableData = res.data.result
                            }

                        }else {
                            this.$message.error("登陆失效")
                            window.sessionStorage.clear()
                            this.$router.push("/")
                        }
                }).catch(err=>{
                    this.$message.error("验证网络出错")
                    window.sessionStorage.clear()
                    this.$router.push("/")
                })
            },
            searchfile(){
                if (this.filename == ""){
                    this.$message.error("请输入部分或完整文件名")
                    return
                }
                const url = "/pan/file/getbyname/"+this.filename
                this.$ajax.get(url,
                    {headers:{
                        "web-token":window.sessionStorage.getItem("web-token")
                        }}).then(res=>{
                    if (res.data.code == 200){
                        this.tableData = res.data.result
                    }else if (res.data.code == 4021){
                        this.tableData  = []
                    } else {
                        this.$message.error("登陆失效")
                        window.sessionStorage.clear()
                        this.$router.push("/")
                    }
                }).catch(err=>{
                    this.$message.error("验证网络出错")
                })

            },
            downloadfile(row){
                // const url = "/pan/file/download/"+row.file_id+"/"+window.sessionStorage.getItem("web-token")
                // window.location.href = url
                const url = "/pan/file/url/genarate/"+row.file_id
                this.$ajax.get(url,
                    {
                        headers:{
                            "web-token":window.sessionStorage.getItem("web-token")
                        }
                    }).then(res=>{
                    if (res.data.code == 200){
                        window.location.href =  res.data.token
                    }else {
                        this.$message.error("登陆失效")
                        window.sessionStorage.clear()
                        this.$router.push("/")
                    }
                }).catch(err=>{
                    this.$message.error("网络出错")
                })
            },
            sharefile(row){
                //const url = "/pan/file/share/"+row.file_id
                const url = "/pan/file/url/genarate/"+row.file_id
                this.$ajax.get(url,
                    {
                        headers:{
                            "web-token":window.sessionStorage.getItem("web-token")
                        }
                    }).then(res=>{
                        if (res.data.code == 200){
                            const h = this.$createElement;
                            this.$notify({
                                title: '文件链接（一小时有效）',
                                // message: h('i', { style: 'color: teal'}, 'http://120.26.78.161:5656/pan/file/downloadbyother/'+res.data.token),
                                message: h('i', { style: 'color: teal'}, res.data.token),

                            });
                        }else {
                            this.$message.error("登陆失效")
                            window.sessionStorage.clear()
                            this.$router.push("/")
                        }
                }).catch(err=>{
                    this.$message.error("网络出错")
                })
            },
            remove(row){
                this.$confirm('确定删除该文件？将无法找回', '确认信息', {
                    distinguishCancelAndClose: true,
                    confirmButtonText: '确认',
                    cancelButtonText: '放弃'
                })
                    .then(() => {
                        const url = "/pan/file/remove/"+row.file_id
                        this.$ajax.get(url,
                            {
                                headers:{
                                    "web-token":window.sessionStorage.getItem("web-token")
                                }
                            }).then(res=>{
                            if( res.data.code == 200){
                                this.$message.info("删除成功")
                                this.getfilelist()
                                this.getUserCapacity()
                            }else {
                                this.$message.error("登陆失效")
                                window.sessionStorage.clear()
                                this.$router.push("/")
                            }
                        }).catch(err=>{
                            this.$message.error("删除网络出错")
                            // window.sessionStorage.clear()
                            // this.$router.push("/")
                        })
                    })
                    .catch(action => {
                        this.$message({
                            type: 'info',
                            message: "已取消删除"
                        })
                    });

            },
            logout(){
                const url = "/pan/user/logout"
                this.$ajax.get(url,
                    {
                        headers:{
                            "web-token":window.sessionStorage.getItem("web-token")
                        }
                    })
                this.$router.push("/")
                window.sessionStorage.clear()
            },
            ajaxControl(){
                this.$ajax.interceptors.request.use(config => {
                    this.loading = true
                    return config
                }, error => {
                    this.loading = true
                    return Promise.reject(error)
                })

                // 添加响应拦截器
                this.$ajax.interceptors.response.use(response => {
                    this.loading = false
                    return response
                }, error => {
                    this.loading = false
                    return Promise.reject(error)
                })
            },
            getusername(){
                const url = "/pan/user/info"
                this.$ajax.get(url,
                    {
                        headers:{
                            "web-token":window.sessionStorage.getItem("web-token")
                        }
                    }).then(res=>{
                        if (res.data.code == 200){
                            this.username = res.data.data
                        }else {
                            this.username = "Me"
                        }
                }).catch(err=>{
                    this.$message.error("查询网络出错")
                })
            }
        },
        name: "index",

    }
</script>

<style scoped>
    /deep/.el-upload-dragger {
        background-color: #fff;
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        -webkit-box-sizing: border-box;
        /* box-sizing: border-box; */
        width: 120%;
        height: 180px;
        text-align: center;
        /* position: relative; */
        /* overflow: hidden; */
    }
    /deep/.el-notification__content {
        font-size: 14px;
        line-height: 21px;
        margin: 6px 0 0;
        color: #606266;
        text-align: justify;
        word-break: break-all;
        /* word-wrap: break-word; */
    }
</style>
