<template>
    <div :style="bgpic" style="min-width: 1000px;min-height: 500px;">
        <div style="height: 100vh;width: 100%">

<!--            <el-button type="success" plain style="float: right;margin-right: 90px;margin-top:70px;color: #303133;border-radius: 10px;"@click="registerDialog = true">注册</el-button>-->

            <div  style="width: 100%;padding-top: 10%;height: 80%;text-align: center;">

                <el-form style="margin:0px auto;width: 25%;background-color: Transparent;text-align: center;min-width: 300px">
                    <h1 style="color: #303133;font-size: 30px;color: #606266;padding-left: 10%;color: #C0C4CC">CloudFolder</h1>
                    <el-form-item  label="账号" >
                        <el-input style="width: 80%" v-model="userlogininfo.userId" ></el-input>
                    </el-form-item>
                    <el-form-item label="密码" >
                        <el-input style="width: 80%" type="password" v-model="userlogininfo.password"  @keyup.enter.native="userLogin"></el-input>
                    </el-form-item>
                    <el-form-item style="padding-left: 10%">
                        <el-button type="primary" style="width: 40%" @click="userLogin">登录</el-button>
                        <el-button type="info" style="width: 40%" @click="registerDialog = true">注册</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>


<!--        弹窗-->
        <el-dialog  :visible.sync="registerDialog" width="30%" style="min-width: 200px">
            <el-form  style="text-align: center">
                <h3 >用户注册</h3>
                <el-form-item label="设置账号" >
                    <el-input style="width: 70%" v-model="userregisterinfo.id"></el-input>
                </el-form-item>
                <el-form-item label="设置密码" >
                    <el-input style="width: 70%" type="password" v-model="userregisterinfo.pwd"></el-input>
                </el-form-item>
                <el-form-item label="确认密码" >
                    <el-input style="width: 70%" type="password" v-model="userregisterinfo.enpwd"></el-input>
                </el-form-item>
                <el-form-item label="输入姓名" >
                    <el-input style="width: 70%" v-model="userregisterinfo.name"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" round class="ensureregister" icon="el-icon-check" @click="ensureregis">确定注册</el-button>
                </el-form-item>
            </el-form>
            <div slot="footer" >

            </div>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                registerDialog : false,
                userregisterinfo:{id:'',pwd:'',enpwd:'',name:''},
                userlogininfo:{userId:'',password:''},
                bgpic:{
                    backgroundImage:'url(' + require('../assets/filelogin.jpg') + ')',
                    backgroundRepeat:'no-repeat',
                    backgroundSize:'100% 100%',


                },
            }
        },
        methods:{
            userLogin(){
                if (this.userlogininfo.userId == ""||this.userlogininfo.password == ""){
                    this.$message.error("请填写完整")
                    return
                }
                const url = "/pan/user/login"
                this.$ajax.post(url,{user_id:this.userlogininfo.userId,password:this.userlogininfo.password})
                    .then(res=>{
                        console.log(res.data)
                        if (res.data.code == 200){

                            this.$message.success("验证成功")
                            //登陆成功，存储token
                            window.sessionStorage.setItem("web-token",res.data.token)

                            this.$router.push("/index")
                        }else if (res.data.code == 4002){
                            this.$message.error("用户名密码错误")
                        }else {
                            this.$message.error("登陆出错")
                        }
                    }).catch(err=>{
                    this.$message.error("网络出错")
                })
            },
            ensureregis(){
                if (this.userregisterinfo.id == ""||this.userregisterinfo.name == ""||this.userregisterinfo.pwd == ""||this.userregisterinfo.enpwd == ""){
                    this.$message.error("请输入完整")
                    return
                }
                if (this.userregisterinfo.pwd != this.userregisterinfo.enpwd){
                    this.$message.error("两次密码不一致")
                    this.userregisterinfo.pwd = ''
                    this.userregisterinfo.enpwd = ''
                    return;
                }
                const url = "/pan/user/register"
                this.$ajax.post(url,{user_id:this.userregisterinfo.id,password:this.userregisterinfo.pwd,username:this.userregisterinfo.name})
                    .then(res=>{
                        if (res.data.code == 200){
                            this.$message.success("注册成功")
                            this.registerDialog = false
                        }else if(res.data.code == 4006){
                            this.$message.success("用户名已存在")
                        }else {
                            this.$message.success("注册出错")
                        }
                    }).catch(err=>{
                        this.$message.error("注册网路出错")
                })
            }
        },
        name: "login"
    }
</script>

<style scoped>

</style>
